
.custom-alert-danger{
  color: #a94442;
  background-color: #f2dede;
  border-color: #ebccd1;
  clear: both;
  margin-bottom: 5px;
  pointer-events: auto;
  padding: 15px;
  border: 1px solid transparent;
  border-radius: 4px;
  text-align: center;
  width: auto;
  float: none;
  font-size: 14px;
}



